.container {
  width: calc(100% - 440px);
  margin: 20px 20px 0 420px; }
  .container h3 {
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    margin-left: 210px;
    font-size: 30px;
    font-weight: 400;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s; }
  .container.withoutElements h3 {
    opacity: 1; }



